import React from 'react';
import { Route } from 'react-router-dom';

import AdminResources from './Resources';
import AdminLesson from './Lesson';
import AdminActivities from './Activities';
import Nav from './components/Nav';
import TransferUsers from './components/TransferUsers';
import TasksPending from './components/TasksPending';

// import classnames from 'classnames';

const Admin = () => {
  return (
    <div className="admin">
      <Nav />
      <Route exact path="/admin/lesson">
        <AdminLesson />
      </Route>
      <Route exact path="/admin/resources">
        <AdminResources />
      </Route>
      <Route exact path="/admin/users">
        <AdminActivities />
      </Route>
      <Route exact path="/admin">
        <TransferUsers />
        <TasksPending />
      </Route>
    </div>
  );
};

export default Admin;
