import React from 'react';
import { usersRef, firebase, tasksRef } from '../../../firebase';

import { format } from 'date-fns';
import MailIcon from '@material-ui/icons/Mail';
import ReactMarkdown from 'react-markdown';
import DeleteIcon from '@material-ui/icons/Delete';
import {
  Avatar,
  Badge,
  Button,
  FormControl,
  Icon,
  Input,
  InputBase,
  InputLabel,
  Modal,
  Select,
  TextField,
} from '@material-ui/core';

export interface TaskItemProps {
  status: 'completed' | 'rejected' | 'pending';
  id: string;
  number: number;
  createdAt: Date;
  responseAt?: Date;
  newTask: boolean;
  description?: string;
}
const AdminActivities = () => {
  const [acceptedUsers, setAcceptedUsers] = React.useState<any>([]);
  const [userTasks, setUserTasks] = React.useState<any[]>([]);
  const [userMessages, setUserMessages] = React.useState<any[]>([]);
  const [currentUser] = React.useState<any>();
  const [currentTaskId, setCurrentTaskId] = React.useState<any>();
  const [currentTaskInfo, setCurrentTaskInfo] = React.useState<any>();
  const [value, setValue] = React.useState<any>('');
  const [valueModal, setValueModal] = React.useState<any>('');
  const [openModal, setOpenModal] = React.useState<any>(false);
  const [lessenNumber, setLessenNumber] = React.useState<any>();
  const [statusTask, setStatusTask] = React.useState<any>();

  React.useEffect(() => {
    usersRef.where('accepted', '==', true).onSnapshot(function (querySnapshot: any) {
      const user: any[] = [];

      querySnapshot.forEach(function (doc: any) {
        user.push(doc.data());
      });
      setAcceptedUsers(user);
    });
  }, []);

  const onGetUserTasks = (id: any) => {
    setUserMessages([]);
    setCurrentTaskInfo(undefined);

    // setCurrentUser(acceptedUsersRef.doc(id));
    console.log(id);
    tasksRef
      .where('uid', '==', id)
      .orderBy('createdAt', 'asc')
      .onSnapshot(function (querySnapshot: any) {
        const items: TaskItemProps[] = [];
        querySnapshot.forEach(function (doc: any) {
          items.push({
            ...doc.data(),
            id: doc.id,
          });
        });
        setUserTasks(items);
      });
  };

  const onGetMessages = ({ id, reference, decision }: any) => {
    setCurrentTaskId(id);
    setCurrentTaskInfo({ reference, decision });
    currentUser
      .collection('tasks')
      .doc(id)
      .collection('messages')
      .orderBy('createdAt', 'asc')
      .onSnapshot(function (querySnapshot: any) {
        const messages: any[] = [];
        querySnapshot.forEach(function (doc: any) {
          messages.push({
            ...doc.data(),
            id: doc.id,
          });
        });
        setUserMessages(messages);
      });
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue(event.target.value);
  };

  const handleChangeModal = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValueModal(event.target.value);
  };

  const handleChangeStatusTask = (event: React.ChangeEvent<{ value: unknown }>) => {
    setStatusTask(String(event.target.value) || '');
  };

  const handleChangeLessenNumber = (event: React.ChangeEvent<{ value: unknown }>) => {
    setLessenNumber(Number(event.target.value) || '');
  };

  const onAddMessage = () => {
    if (currentUser) {
      currentUser.collection('tasks').doc(currentTaskId).collection('messages').add({
        name: 'Царевич',
        text: value,
        profilePicUrl: firebase.auth().currentUser?.photoURL,
        createdAt: new Date(),
        newMessage: true,
      });
    }

    setValue('');
  };
  const onDeleteMessage = (id: any) => {
    if (currentUser) {
      currentUser
        .collection('tasks')
        .doc(currentTaskId)
        .collection('messages')
        .doc(id)
        .delete()
        .then(() => {
          console.log('удалено');
        })
        .catch(function (error: any) {
          console.error('Error removing document: ', error);
        });
    }
  };

  const onDeleteTask = (id: any) => {
    if (currentUser) {
      currentUser
        .collection('tasks')
        .doc(id)
        .delete()
        .then(() => {
          console.log('удалено');
        })
        .catch(function (error: any) {
          console.error('Error removing document: ', error);
        });
    }
  };

  const handleCloseModal = () => {
    setOpenModal(false);
    setValueModal('');
  };

  const handleOpenModal = () => {
    setValueModal('');
    setOpenModal(true);
  };

  const onAddTask = (number: number, status: string) => {
    setOpenModal(false);
    if (!!currentUser) {
      currentUser.collection('tasks').add({
        number,
        status,
        createdAt: new Date(),
        decision: valueModal,
        reference: '',
        newTask: true,
      });
    }
  };
  return (
    <div>
      <div className="admin__table">
        <div className="admin__table__header">
          {acceptedUsers.map((user: any) => {
            return (
              <div className="admin__table__header--item" key={user.uid || 0}>
                <Button
                  startIcon={<Avatar src={user.photoURL} />}
                  variant="contained"
                  color="primary"
                  onClick={() => onGetUserTasks(user.uid)}
                >
                  {user.displayName || user.email}
                </Button>
              </div>
            );
          })}
        </div>
        <div className="admin__table__content">
          <div className="admin__table__content--tasks scrollbar">
            {!!userTasks.length &&
              userTasks.map((task: any) => {
                return (
                  <div
                    className={`admin__table__content--task admin__${task.status}`}
                    key={task.createdAt}
                  >
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                      }}
                    >
                      <div>
                        <span style={{ fontWeight: 500, fontSize: 24 }}>{task.status}</span>
                        <span
                          style={{
                            color: 'gray',
                            fontWeight: 500,
                            fontSize: 19,
                            paddingLeft: 10,
                          }}
                        >
                          {format(task.createdAt.toDate(), 'dd.MM.yyyy - HH:mm')}
                        </span>
                      </div>
                      <span style={{ fontWeight: 600, fontSize: 24 }}>
                        {'Урок №' + task.number}
                      </span>
                    </div>

                    <p>{'ссылка на гитхаб: ' + task.reference}</p>
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                      }}
                    >
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => onGetMessages(task)}
                      >
                        Сообщения
                        <Badge
                          style={{ color: 'grey', marginLeft: 5 }}
                          color="secondary"
                          badgeContent={0}
                          variant="dot"
                        >
                          <MailIcon />
                        </Badge>
                      </Button>

                      <Button
                        color="secondary"
                        onClick={() => onDeleteTask(task.id)}
                        startIcon={<DeleteIcon />}
                      >
                        DELETE
                      </Button>
                    </div>
                  </div>
                );
              })}

            <div className="admin__task__block">
              {!!currentUser ? (
                <Button onClick={handleOpenModal} variant="contained" fullWidth color="secondary">
                  Добавить таску
                </Button>
              ) : (
                <div>Выберите пользователя</div>
              )}
            </div>
          </div>

          <div className="admin__table__content--messages scrollbar">
            {!!currentTaskInfo && (
              <div className="admin__table__content--messages--info">
                <div>Ссылка на гитхаб: {currentTaskInfo.reference || 'Отсутствует'}</div>
                <div>Сообщение пользователя: {currentTaskInfo.decision || 'Отсутствует'}</div>
              </div>
            )}

            {!!userMessages.length &&
              userMessages.map((message) => {
                return (
                  <div key={message.id} className="admin__message__item">
                    <div className="admin__message__user">
                      <div className="admin__message__user--info">
                        <img
                          className="admin__message__avatar"
                          alt="user avatar"
                          src={message.profilePicUrl}
                        />
                        <div className="admin__message__title">
                          <span className="admin__message__name">{message.name}</span>
                          <span className="admin__message__info">
                            Отправлено{' '}
                            {format(message.createdAt.toDate(), 'dd.MM.yyyy, в HH.mm.ss')}
                          </span>
                        </div>
                      </div>
                      <div>
                        <Button
                          color="secondary"
                          onClick={() => onDeleteMessage(message.id)}
                          startIcon={<DeleteIcon />}
                        >
                          DELETE
                        </Button>
                      </div>
                    </div>
                    <div className="admin__message__item-text">
                      <ReactMarkdown source={message.text} />
                    </div>
                  </div>
                );
              })}

            {!!currentTaskId && (
              <div className="admin__message__chat">
                <div className="admin__message__chat-field">
                  <InputBase
                    className="scrollbar"
                    rows={4}
                    rowsMax={10}
                    value={value}
                    onChange={handleChange}
                    fullWidth
                    inputProps={{ 'aria-label': 'naked' }}
                    multiline
                    placeholder="Комментарий"
                  />
                </div>

                <div className="admin__message__button">
                  <Button
                    color="primary"
                    disabled={value.length === 0 || !currentTaskInfo}
                    variant="contained"
                    onClick={onAddMessage}
                    endIcon={<Icon>send</Icon>}
                  >
                    Отправить
                  </Button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      {/* Модальное окно ADD TASK */}
      <Modal
        className="admin_activities__modal"
        open={openModal}
        onClose={handleCloseModal}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        <div className="item-wrapper admin__modal">
          <FormControl className="admin_activities__formControl">
            <InputLabel htmlFor="demo-dialog-native">Решение</InputLabel>
            <Select
              native
              value={statusTask}
              onChange={handleChangeStatusTask}
              input={<Input id="demo-dialog-native" />}
            >
              <option aria-label="None" value="" />
              <option value="completed">Выполнено</option>
              <option value="rejected">Отклонено</option>
            </Select>
          </FormControl>
          <TextField
            value={lessenNumber}
            onChange={handleChangeLessenNumber}
            label="Lessen number"
            variant="outlined"
          />
          <h2>Выберите задание</h2>
          <p>Комментарий: </p>
          <div className="admin__modal-field">
            <InputBase
              className="scrollbar"
              rows={8}
              value={valueModal}
              onChange={handleChangeModal}
              fullWidth
              inputProps={{ 'aria-label': 'naked' }}
              multiline
              placeholder="Комментарий"
            />
          </div>
          <div className="admin__modal-button">
            <div className="button--cancel">
              <Button onClick={handleCloseModal} variant="contained">
                Отмена
              </Button>
            </div>
            <div className="button--send">
              <Button
                onClick={() => onAddTask(lessenNumber, statusTask)}
                disabled={!statusTask}
                variant="contained"
              >
                Отправить
              </Button>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default AdminActivities;
